import React, { useEffect } from "react";
import {
  makeStyles,
  TextField,
  Button,
  FormControl,
  InputAdornment,
  InputLabel,
  OutlinedInput
} from "@material-ui/core";
import clsx from "clsx";
import {
  useStore,
  useFetchData
} from "../components/custom-hooks/custom-hooks";


const useStyles = makeStyles(theme => ({
  submit: {
    margin: theme.spacing(3, 0, 2),
    lineHeight: "2.5rem",
    fontSize: 16
  },
  page: {
    marginTop: theme.spacing(6),
    marginBottom: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  inputText: {
    width: "300px", // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  }
}));

const Regist = props => {
  const classes = useStyles();
  const { input, updateField, onSubmit, setInput,onValidate } = useFetchData("", "");
  const { store,onChangeStore } = useStore();
  let checked = false;
  useEffect(() => {
    setInput({ ...input, u_No: store.u_No });
  }, []);
  const onClickEvent = async event => {
    if(checked){
      let result = await onSubmit(store.url + "/device");
      if (result !== false) {
        onChangeStore({...store,u_Last : result})
        alert(" .");
        props.history.goBack();
      } else {
        alert(" .");
      }
    }else{
      alert("  .");
    }
  };
  const onCheckEvent = async event => {
    if(input.SerialNo === undefined || input.SerialNo === ""){
      alert(" !!");
    }else{
      const result = await onValidate(store.url + "/device/check/"+input.SerialNo);
      if(result){
        alert(" .")
        checked = true;
      }else{
        alert("  .")
      }
    }
  }
  return (
    <div className={classes.page}>
      <h3>  </h3>
      <div className={classes.inputText}>
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          id="d_Name"
          label=""
          name="d_Name"
          autoFocus
          onChange={updateField}
          value={input.d_Name}
        />

        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          id="d_Age"
          label=""
          name="d_Age"
          value={input.d_Age}
          onChange={updateField}
        />
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          id="d_Species"
          label=""
          name="d_Species"
          value={input.d_Species}
          onChange={updateField}
        />
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          id="d_Weight"
          label=""
          name="d_Weight"
          value={input.d_Weight}
          onChange={updateField}
        />

        <FormControl
          className={clsx(classes.margin, classes.textField)}
          variant="outlined"
          fullWidth
          margin="normal"
        >
          <InputLabel htmlFor="outlined-adornment-password" required>
             S/N
          </InputLabel>
          <OutlinedInput
            id="outlined-adornment-password"
            value={input.SerialNo}
            onChange={updateField} //     
            name="SerialNo"
            required = {true}
            endAdornment={
              <InputAdornment position="end">
                <Button onClick = {onCheckEvent}></Button>
              </InputAdornment>
            }
            labelWidth={104}
          />
        </FormControl>
        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={onClickEvent}
        >
           
        </Button>
      </div>
    </div>
  );
};

export default Regist;
